<template>
  <div class="van-radio-dict">
    <span class="title">基本使用</span>
    <van-radio-dict direction="horizontal" @dictChange="handleChange" dictType="AAC004" v-model="value1" ></van-radio-dict>
    <span class="title">过滤数据1</span>
    <van-radio-dict direction="horizontal" @dictChange="handleChange" :dictType="{type: 'AAC004', filters: '9', reverse: true}" v-model="value2" ></van-radio-dict>
    <span class="title">过滤数据2</span>
    <van-radio-dict direction="horizontal" @dictChange="handleChange" :filterDataFun="filterDataFun" :dictType="'AAC004'" v-model="value3" ></van-radio-dict>
    <span class="title">禁用数据1</span>
    <van-radio-dict direction="horizontal" @dictChange="handleChange" :disableObj="{disableValue: '9'}" :dictType="'AAC004'" v-model="value4" ></van-radio-dict>
    <span class="title">禁用数据2</span>
    <van-radio-dict direction="horizontal" @dictChange="handleChange" :disabledDataFun="disabledDataFun" :dictType="'AAC004'" v-model="value5" ></van-radio-dict>
    <span class="title">禁用+过滤</span>
    <van-radio-dict direction="horizontal" @dictChange="handleChange"  :disableObj="{disableValue: '9'}"  :dictType="{type: 'AAC004', filters: '1', reverse: true}" v-model="value6" ></van-radio-dict>
    <span class="title">赋值</span>
    <van-radio-dict direction="horizontal" @dictChange="handleChange"    :dictType="'AAC004'" v-model="value7" ></van-radio-dict>
    <span class="title">keyValue</span>
    <van-radio-dict direction="horizontal" keyValue @dictChange="handleChange"   :dictType="'AAC004'" v-model="value8" ></van-radio-dict>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      value6: "",
      value7: "1",
      value8: ""
    }
  },
  methods: {
    handleChange(val) {
      console.log(val)
    },
    filterDataFun(list) {
      return list.filter(item => ["1", "2"].includes(item.value))
    },
    disabledDataFun(list) {
      return list.map(item => {
        return {
          ...item,
          disabled: ["9"].includes(item.value)
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.van-radio-dict{
  padding:  0 16px;
  .title {
    line-height: 50px;
  }
}
</style>